@import url('https://fonts.googleapis.com/css2?family=Radio+Canada:wght@300;400;500;600;700&display=swap');

:root {
  --primary-dark: #293A80;
  --primary-light: #6391dc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Radio Canada', sans-serif;
}

ul {
list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
}

h2{
  font-size: 2rem;
}


.container {
  max-width: 1240px;
  margin: auto;
  padding: 0 1rem;
}


button {
  background-image: linear-gradient(45deg,var(--primary-dark) 0, var(--primary-light) 100%);
  border: 1px solid var(--primary-dark);
  color: #fff;
  padding: .7rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-family: 'Radio Canada', sans-serif;
}



@media screen and (max-width: 940px)  {
    h1 {
      font-size: 2.5rem;
    }
    h2 {
      font-size: 1.5rem;
    }

}